import React from 'react'
import CommentsIcon from '../icons/social/comments'
import VotesIcon from '../icons/social/votes'
import SharedIcon from '../icons/social/shared'
import Levels from './levels'


const Social = (props) => {
    const {social =  {
        nbComments: 0,
        nbVotes: {
            up: 0,
            down: 0
        },
        nbShared: 0,
        levels: undefined,
        }} = props;

    return (
       <>
            <div className="container">
                            
                <div className="actions">
                    <div className="align comments"><CommentsIcon /> <div className="nb">{social.nbComments}</div></div> 
                    <div className="align votes"><div className="nb">{social.nbVotes.down}</div><VotesIcon /><div className="nb">{social.nbVotes.up}</div></div> 
                    <div className="align shared"><SharedIcon /><div className="nb">{social.nbShared}</div></div>
                </div>
                <div className="score">
                    <Levels levels={social.levels}/>
                </div>
                    
            </div>
                   

            <style jsx>
                {`  
                    .actions {
                        width: 160px;
                        height: 100%;
                        align-items: center;
                        display: flex;
                        flex-direction: row;
                        justify-content: space-between;

                        font: 8px/11px Arial;
                        letter-spacing: 0;
                        color: #000;
                    }

                    .container {
                        align-items: center;
                        display: flex;
                        flex-direction: row;
                        justify-content: space-between;

                        width:100%;
                    }

                    .comments {
                    }

                    .nb {
                        margin-right: 3px;
                        margin-left: 3px;
                    }

                    .align {
                        align-items: center;
                        display: flex;

                    }
                  
                `}

            </style>


        </>

    )
}

export default Social;